import React, { useEffect, Component } from 'react';
import axios from 'axios';


export default class searchCity extends Component {

constructor(props) {
  super(props);
  this.onChangeCity = this.onChangeCity.bind(this);
  this.onChangeUser = this.onChangeUser.bind(this);

  this.state = {
    cityName: '',
    cities: [],
    userName: '',
    users: []
    };
  } 

componentDidMount() {

  axios.get('https://rate-my-city.herokuapp.com/listCities')
  .then(response => {
  if (response.data.length > 0) {
  this.setState({
  cities: response.data.map(cty => cty.name),
  cityName: response.data[0].name
  })
  }
})
.catch(function (error) {
  console.log(error);
});

axios.get('https://rate-my-city.herokuapp.com/listUsers')
.then(response => {
if (response.data.length > 0) {
  this.setState({
    users: response.data.map(usr => usr.userName),
    userName: response.data[0].userName
    })
  }
})
.catch(function (error) {
  console.log(error);
  });
}

onChangeCity(e) {
  this.setState({
  cityName: e.target.value
  });
}

onChangeUser(e) {
  this.setState({
    userName: e.target.value
    });
}

onSubmit(e) {
  e.preventDefault();

const searchInfo = {
  cityName: this.state.cityName,
  userName: this.state.userName
}

console.log(searchInfo);


this.setState ({
    cityName: '',
    userName: ''
  })
}



render () {
    return (
      <div className="input-group mt-1 mb-2">
      <form onSubmit={this.onSubmit}>
      <div className="form-group">
    <select
      required
      className="form-control"
      value={this.state.cityName}
      onChange={this.onChangeCity}>{
          this.state.cities.map(function(cty) {
              return <option
            key={cty}
            value={cty}>{cty}
    </option>;
    })
    }
    </select>
</div>



</form>

<input className="form-control" placeholder="Search by State" />

<div className="form-group">
  <select
    required
    className="form-control"
    value={this.state.userName}
    onChange={this.onChangeUser}>{
      this.state.users.map(function(usr) {
      return <option
      key={usr}
      value={usr}>{usr}
  </option>;
  })
}
</select>
</div>



{/* <input className="form-control" placeholder="Search by Username" /> */}
  <button className="btn text-white fw-bold btn-outline-dark btn-secondary" >Search</button>
</div>


)
}



};

